/* src/app/features/live/live-page.component.css */
.live-container {
  padding: 3rem 1rem;
}
.sessions-list {
  margin-top: 2.25rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}
.session-card {
  position: relative;
  background:
    linear-gradient(
      140deg,
      rgba(26, 35, 50, 1) 0%,
      rgba(15, 23, 36, 1) 90%);
  padding: 1.75rem 1.5rem;
  border-radius: 16px;
  color: #fff;
  border: 1px solid var(--border);
  box-shadow: 0 12px 30px -10px rgba(0, 0, 0, .5);
  transition:
    transform .35s ease,
    box-shadow .35s ease,
    border-color .35s;
  overflow: hidden;
}
.session-card:before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    radial-gradient(
      circle at 20% 20%,
      rgba(11, 110, 255, 0.18),
      transparent 60%);
}
.session-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 18px 42px -12px rgba(0, 0, 0, .55);
  border-color: var(--brand-primary);
}
.session-card h3 {
  margin: 0 0 .6rem;
  font-size: 1.2rem;
  font-weight: 600;
}
.session-card p {
  margin: 0 0 1rem;
  font-size: .95rem;
  line-height: 1.55;
  color: var(--text-secondary);
}
.session-meta {
  display: flex;
  gap: 1rem;
  font-size: .7rem;
  text-transform: uppercase;
  letter-spacing: .5px;
  color: var(--text-muted);
  margin-bottom: .75rem;
}
.session-actions {
  margin-top: .75rem;
}
.session-actions button {
  background:
    linear-gradient(
      90deg,
      #0B6EFF,
      #00C28A);
  color: #fff;
  border: none;
  padding: .6rem 1.1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  width: 100%;
  transition: filter .25s;
}
.session-actions button:hover {
  filter: brightness(1.1);
}
